#jeeFaceFilterCanvas {
  /* YOU SHOULD KEEP the transform: translateZ(-4000px) and the transform-style: preserve-3d;
  OTHERWISE THE DIV WILL BE BEHIND THE CANVAS WITH IOS
  */
   -webkit-transform: translateZ(-4000px) translate(-50%, -50%) rotateY(180deg);
           transform: translateZ(-4000px) translate(-50%, -50%) rotateY(180deg);
   -webkit-transform-style: preserve-3d;
           transform-style: preserve-3d;
}

a {color: #eee; text-decoration: none}
a:hover {color: blue;}
body {overflow: auto; overflow-y: auto;
    background-color: white;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    margin: 0px;}
#jeeFaceFilterCanvas {
    z-index: 10;
    position: absolute; 
    height: 100%;
    width: 100%;
    left: 50%;
    top: 50%;
    width: 100vmin;
    -webkit-transform: translate(-50%, -50%) rotateY(180deg);
            transform: translate(-50%, -50%) rotateY(180deg);
} 
@media (max-width: 787px) {
    #jeeFaceFilterCanvas {
        right: 0px;
        top: 60px;
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
}

.overlay {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #eeeeee;
  text-align: center;
  position: fixed;
  z-index: 10000;
  text-align: center;
}


.overlay .header h1, .tos h2, .tos p {
  margin-top:2em;
  text-transform:lowercase;

}

.overlay .logo {
  margin-top:2em;
}

.overlay .logo h1 {
  font-size: 5em;

}

.overlay .initCamera {

  text-transform:lowercase;
  margin-top:2em;
  padding:.5em;
  background-color: none;
  background: none;
  box-shadow: none;
  border: 1px #000 solid;
  font-size: 2em;
  font-weight:300;
}
.sharingModal {
  width: 90%;
  height: 95%;
  margin: 2.5% 5%;
  padding: .25em;
  background-color: #eeeeee;
  text-align: center;
  position: fixed;
  top: 0px;
  z-index: 10000;
  text-align: center;
}

.sharingModal .closeSharingModal {
  margin: 1%;
  background-color: none;
  background: none;
  box-shadow: none;
  border: 0;
  font-size: 2em;
}
.productMenu {
  width:100%;
  background-color:rgba(255, 255, 255, 0.7);
  text-align:center;
  height:2em;
  position:fixed;
  margin-top:3em;
  padding:0;
  z-index:1000;
}

.productMenu .productMenuItem {
padding:1em;
margin-left:1em;
margin-right:1em;
text-transform: lowercase;
}
